    &.is-group {
      th {
        background: #f2f6ff;
      }
    }
}
.el-table thead .cell {
    font-size: 14px;
    color: #222222;
    letter-spacing: 0;
    line-height: 14px;
    font-weight: 400;
}
/* 将 斑马的背景色修改 */
.el-table--striped .el-table__body tr.el-table__row--striped td {
    background: #f6f9fc;
}
/* 修改表格行上的hover颜色 */
.el-table--enable-row-hover .el-table__body tr:hover>td {
    background-color: #ecf2f9;
}
/* 将table每行的高度修改为40 */
/* 将默认文字居中 */
.el-table td, .el-table th {
    text-align: center;
    padding: 11px 0;
}
/* 修改table行高 */
.el-table .cell {
    color: #333;
    line-height: 17px;
}
/* 让表格内的text按钮的高度缩小，不让其影响表格行高 */
.el-table .cell .el-button.el-button--text {
    padding: 0px 15px;
}
/* 排序状态下的表头高度问题 */
.el-table .caret-wrapper {
    height: 17px;
}
.el-table .sort-caret.ascending {
    top: -3px;
}
.el-table .sort-caret.descending {
    bottom: -2px;
}
/*****************页面样式*******************/
.box {
    margin-bottom: 20px;
}
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
</style>



